.card-button {
    height: 120px;
    border-radius: 10px;
    -webkit-box-shadow: 0 0 4px 4px rgba(0, 0, 0, 0.01);
    box-shadow: 0 0 4px 4px rgba(0, 0, 0, 0.01);
    width: 100%;
    margin-top: 20px;
    border: 1px solid #e64646;
    margin-left: 32px;
    margin-right: 32px;
}

.card-button .inner {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
    flex-direction: row;
    -ms-flex-wrap: nowrap;
    flex-wrap: nowrap;
    -ms-flex-pack: justify;
    justify-content: start;
    border: 1px solid #3f8ae0;
}

.card-button .img {
    width: 80px;
    min-width: 80px;
    height: 80px;
    margin: 20px 20px 20px 16px;
    border: 1px solid #4bb34b;
}

.card-button .other {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column;
    -ms-flex-pack: center;
    justify-content: center;
    border: 1px solid #792ec0;
}

.card-button .title {
    height: 22px;
    font-size: 17px;
    font-weight: 600;
    line-height: 1.29;
    letter-spacing: -0.4px;
    color: #fff;
    margin-bottom: 6px;
    margin-right: 16px;
    border: 1px solid #9e8d6b;
}

.card-button .desc {
    opacity: 0.72;
    font-size: 13px;
    line-height: 1.23;
    letter-spacing: -0.1px;
    color: #fff;
    margin-right: 16px;
    margin-bottom: 4px;
    border: 1px solid #ffa000;
}